<template>
  <div id="app">
    <!-- HW Ïª¥Ìè¨ÎÑåÌä∏Ïóê Îç∞Ïù¥ÌÑ∞, Ï†ÄÏû• Ìï®Ïàò, ÎπÑÎ∞ÄÎ≤àÌò∏ Ï≤¥ÌÅ¨ Ìï®ÏàòÎ•º propsÎ°ú Ï†ÑÎã¨Ìï©ÎãàÎã§. -->
    <HW 
      :duty-data="dutyData" 
      :save-duty-entry="saveDutyEntry"
      :check-password="checkPassword"
    />
  </div>
</template>

<script>
import HW from './components/HW.vue'
import { initializeApp } from "firebase/app";
import { getMessaging, getToken, onMessage } from "firebase/messaging";
import { 
  getFirestore, 
  collection, 
  onSnapshot, 
  query, 
  doc, 
  setDoc,
  where,
  getDocs
} from "firebase/firestore";

import { getFunctions, httpsCallable } from "firebase/functions";

export default {
  name: 'App',
  components: {
    HW
  },
  data() {
    return {
      db: null, // Firestore Ïù∏Ïä§ÌÑ¥Ïä§Î•º Ï†ÄÏû•
      dutyData: [], // FirestoreÏóêÏÑú Í∞ÄÏ†∏Ïò® Ï∫òÎ¶∞Îçî Îç∞Ïù¥ÌÑ∞Î•º Ï†ÄÏû•Ìï† Î∞∞Ïó¥
      unsubscribe: null // Ïã§ÏãúÍ∞Ñ Î¶¨Ïä§ÎÑà Ìï¥Ï†ú Ìï®ÏàòÎ•º Ï†ÄÏû•
    }
  },
  methods: {
    // ÏïåÎ¶º Î∞úÏÜ° Î°úÏßÅ
    async sendAutoNotification(newName, date, oldName) {
      try {
        const tokenSnapshot = await getDocs(collection(this.db, "fcm_tokens"));
        const tokens = [];
        tokenSnapshot.forEach((doc) => tokens.push(doc.data().token));
        if (tokens.length === 0) return;

        const d = String(date);
        const formattedDate = `20${d.substring(0,2)}ÎÖÑ ${d.substring(2,4)}Ïõî ${d.substring(4,6)}Ïùº`;
        const messageBody = oldName !== "ÏóÜÏùå" && oldName !== newName 
          ? `[${formattedDate}] \nÎãπÏßÅÏûêÍ∞Ä ${oldName}Îãò ‚Üí ${newName}ÎãòÏúºÎ°ú Î≥ÄÍ≤ΩÎêòÏóàÏäµÎãàÎã§.`
          : `[${formattedDate}] \n${newName}ÎãòÏù¥ ÎãπÏßÅÏúºÎ°ú Îì±Î°ùÎêòÏóàÏäµÎãàÎã§.`;

        // ‚≠ê ÏàòÏ†ïÎêú Î∂ÄÎ∂Ñ: "us-central1"ÏùÑ Î™ÖÏãúÌï¥ Ï§çÎãàÎã§.
        const functions = getFunctions(undefined, "us-central1"); 
        const sendFcmNotification = httpsCallable(functions, 'sendFcmNotification');

        const result = await sendFcmNotification({
          tokens: tokens,
          title: "üìÖ ÎãπÏßÅ ÏóÖÎç∞Ïù¥Ìä∏ ÏïåÎ¶º",
          body: messageBody
        });

        console.log("ÏïåÎ¶º ÏÑúÎ≤Ñ ÏùëÎãµ:", result.data);
      } catch (error) {
        console.error("ÏïåÎ¶º ÏöîÏ≤≠ Ï§ë Ïò§Î•ò:", error);
      }
    },

    async saveDutyEntry(entry, docIdToUpdate) {
      if (!this.db) return false;
      
      // const dataToSave = { 
      //   ...entry, 
      //   date: parseInt(entry.date, 10) 
      // };

      // ÏïåÎ¶º Ïó¨Î∂Ä Í∞íÏùÑ Îî∞Î°ú Ï∂îÏ∂úÌïòÍ≥†, DB Ï†ÄÏû• Îç∞Ïù¥ÌÑ∞ÏóêÏÑúÎäî Ï†úÏô∏Ìï©ÎãàÎã§.
      const { sendNotification, ...dataToSave } = entry;
      dataToSave.date = parseInt(entry.date, 10);

      try {
        let oldName = "ÏóÜÏùå"; // Í∏∞Ï°¥ ÎãπÏßÅÏûê Ïù¥Î¶ÑÏùÑ Ï†ÄÏû•Ìï† Î≥ÄÏàò

        // 1. ÏàòÏ†ï Î™®ÎìúÏùº Îïå, Í∏∞Ï°¥ Îç∞Ïù¥ÌÑ∞ÏóêÏÑú Ïù¥Î¶ÑÏùÑ Ï∞æÏïÑÎë°ÎãàÎã§.
        if (docIdToUpdate) {
          const existingEntry = this.dutyData.find(item => item.id === docIdToUpdate);
          if (existingEntry) {
            oldName = existingEntry.name;
          }
        }

        // 2. ÏÉàÎ°úÏö¥ Îç∞Ïù¥ÌÑ∞ Ï†ÄÏû•
        let docRef;
        if (docIdToUpdate) {
          docRef = doc(this.db, "calendar", docIdToUpdate);
        } else {
          docRef = doc(collection(this.db, "calendar"));
        }
        await setDoc(docRef, dataToSave);

        // 3. ÏïåÎ¶º Ìï®Ïàò Ìò∏Ï∂ú Ïãú 'Ïù¥Ï†Ñ Ïù¥Î¶Ñ'Í≥º 'ÏÉà Ïù¥Î¶Ñ'ÏùÑ Í∞ôÏù¥ Î≥¥ÎÉÖÎãàÎã§.
        // this.sendAutoNotification(dataToSave.name, dataToSave.date, oldName);
        if (sendNotification) {
          this.sendAutoNotification(dataToSave.name, dataToSave.date, oldName);
        } else {
          console.log("ÏïåÎ¶º Ï†ÑÏÜ°Ïù¥ ÎπÑÌôúÏÑ±ÌôîÎêòÏñ¥ ÏïåÎ¶ºÏùÑ Î≥¥ÎÇ¥ÏßÄ ÏïäÏïòÏäµÎãàÎã§.");
        }
        
        return true;
      } catch (error) {
        console.error("Ï†ÄÏû• ÏóêÎü¨:", error);
        return false;
      }
    },
    // ÎπÑÎ∞ÄÎ≤àÌò∏ Í≤ÄÏ¶ù Ìï®Ïàò
    async checkPassword(inputPassword) {
      if (!this.db) {
        console.error("Firestore DB is not initialized.");
        return { success: false, message: "DB Ï¥àÍ∏∞Ìôî Ïò§Î•ò." };
      }
      
      try {
        // 1. password ÌïÑÎìúÍ∞Ä ÏûÖÎ†•Îêú Í∞íÍ≥º ÏùºÏπòÌïòÎäî Î¨∏ÏÑúÎ•º Ï∞æÎäî ÏøºÎ¶¨ ÏÉùÏÑ±
        const passwordQuery = query(
          collection(this.db, "editpw"),
          where("password", "==", inputPassword)
        );

        // 2. ÏøºÎ¶¨ Ïã§Ìñâ
        const querySnapshot = await getDocs(passwordQuery);

        // 3. Í≤∞Í≥º ÌôïÏù∏: ÏùºÏπòÌïòÎäî Î¨∏ÏÑúÍ∞Ä 1Í∞ú Ïù¥ÏÉÅ Ï°¥Ïû¨ÌïòÎ©¥ ÏÑ±Í≥µ
        if (!querySnapshot.empty) {
          // ÏùºÏπòÌïòÎäî Î¨∏ÏÑúÎ•º Ï∞æÏïòÏäµÎãàÎã§. (Ïã§Ï†ú Îç∞Ïù¥ÌÑ∞Îäî Ï≤´ Î≤àÏß∏ Î¨∏ÏÑúÏùò ÌïÑÎìúÎ•º ÏÇ¨Ïö©Ìï† ÌïÑÏöî ÏóÜÏù¥ Ï°¥Ïû¨ Ïó¨Î∂ÄÎßå ÌôïÏù∏)
          return { success: true };
        } else {
          // ÏùºÏπòÌïòÎäî Î¨∏ÏÑúÍ∞Ä ÏóÜÍ±∞ÎÇò ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÌãÄÎ¶∞ Í≤ΩÏö∞
          return { success: false, message: "ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§." };
        }
      } catch (error) {
        console.error("ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
        return { success: false, message: `ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ Ï§ë Ïò§Î•ò Î∞úÏÉù: ${error.message}` };
      }
    },


  },
  mounted() {
  const firebaseConfig = {
    apiKey: "AIzaSyDMjFwCNFE72bctk4BNodId8fLLXMQ_Ulw",
    authDomain: "hello-notification-121eb.firebaseapp.com",
    projectId: "hello-notification-121eb",
    storageBucket: "hello-notification-121eb.firebasestorage.app",
    messagingSenderId: "17313072285",
    appId: "1:17313072285:web:ddbd170a5b062f236fc074",
    measurementId: "G-8W89T2V4CF"
  };

  // 1. Firebase Ïï± Ï¥àÍ∏∞Ìôî
  const app = initializeApp(firebaseConfig);
  
  // 2. Firestore Ï¥àÍ∏∞Ìôî
  this.db = getFirestore(app);
  
  // 3. ‚≠ê Messaging Ï¥àÍ∏∞Ìôî (getToken Î≥¥Îã§ Î∞òÎìúÏãú ÏúÑÏóê ÏûàÏñ¥Ïïº Ìï®!)
  const messaging = getMessaging(app);

  // 4. Îç∞Ïù¥ÌÑ∞ Ïã§ÏãúÍ∞Ñ ÏàòÏã† ÏÑ§Ï†ï
  const q = query(collection(this.db, "calendar"), where("date", ">=", 260101), where("date", "<=", 261231));
  this.unsubscribe = onSnapshot(q, (snapshot) => {
    this.dutyData = snapshot.docs.map(doc => ({ 
      id: doc.id, 
      ...doc.data(), 
      date: String(doc.data().date) 
    }));
  });

  // 5. ÌÜ†ÌÅ∞ Í∞ÄÏ†∏Ïò§Í∏∞ Î∞è Ï†ÄÏû• (ÏúÑÏóêÏÑú ÎßåÎì† messaging Î≥ÄÏàòÎ•º Ïó¨Í∏∞ÏÑú ÏÇ¨Ïö©)
  getToken(messaging, { vapidKey: 'BLYnDWIi8AbuQdrJx1ct6EU5hi6yz6dyG4GxFgLvb_2G42mMjdLwF9i_PkcN8csNR3HynhqVu9ZO92_Am_RcuVg' })
    .then(async (currentToken) => {
      if (currentToken) {
        await setDoc(doc(this.db, "fcm_tokens", currentToken), {
          token: currentToken,
          lastUpdated: new Date()
        }, { merge: true });
        console.log("FCM ÌÜ†ÌÅ∞ ÏûêÎèô Ï†ÄÏû• ÏôÑÎ£å");
      }
    }).catch((err) => {
      console.log('ÌÜ†ÌÅ∞ Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå®:', err);
    });

  onMessage(messaging, (payload) => { 
    console.log('Î©îÏãúÏßÄ ÏàòÏã†:', payload); 
  });
},
  beforeUnmount() {
    // Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÌååÍ¥¥ÎêòÍ∏∞ Ï†ÑÏóê Ïã§ÏãúÍ∞Ñ Î¶¨Ïä§ÎÑàÎ•º Ìï¥Ï†úÌï©ÎãàÎã§.
    if (this.unsubscribe) {
      this.unsubscribe();
    }
  }
}
</script>

