.form-grid {
    display: grid; grid-template-columns: 1fr 2fr; gap: 10px; align-items: center; margin-bottom: 15px;
}
.form-grid label { text-align: right; font-weight: bold; color: #555; }
.form-grid input, .form-grid select { padding: 8px; border: 1px solid #ccc; border-radius: 5px; }
.date-display { grid-column: 1 / 3; text-align: center; font-size: 1.1rem; padding: 5px 0; background-color: #e3f2fd; border-radius: 5px; }
.form-buttons { grid-column: 1 / 3; display: flex; justify-content: space-around; margin-top: 10px; }
.save-button, .cancel-button { 
    padding: 10px 30px; border: none; border-radius: 5px; font-weight: bold; cursor: pointer; 
    transition: background-color 0.2s;
}
.save-button { background-color: #03A9F4; color: white; }
.save-button:hover { background-color: #039be5; }
.cancel-button { background-color: #B0BEC5; color: white; }
.cancel-button:hover { background-color: #90A4AE; }
.success-message { color: #4CAF50; margin-top: 10px; font-weight: bold; }

